*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --hero-bg: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    --menu-bg: url(${___CSS_LOADER_URL_REPLACEMENT_1___});

    --fallback-font: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    --main-font-family: "Roboto";

    /* palette */
    --black: #000000;
    --oxford-blue: #14213D;
    --web-orange: #FCA311;
    --platinum: #E5E5E5;
    --white: #FFFFFF;
}

body {
    font-family: var(--main-font-family), var(--fallback-font);
}

.cursor {
    cursor: pointer;
}

/* header */
.header-container {
    background-color: var(--oxford-blue);
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    border-bottom: 1px solid var(--platinum);
}

.header-img:hover {
    transform: scale(1.2);
}

.nav-btn {
    border: none;
    background-color: var(--oxford-blue);
    margin: 15px;
    font-size: 1.8rem;
    color: var(--platinum);
}

.nav-btn:after {
    transition: all ease-in-out .5s;
    background-color: var(--web-orange);
    content: "";
    display: block;
    height: 3px;
    width: 0;
}

.nav-btn:hover:after {
    width: 100%;
}

/* homepage hero section */
.hero-section {
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), var(--hero-bg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    position: relative;
}

.hero-text-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--white);
    text-align: center;
    padding: 20px;
}

.hero-heading {
    font-size: 3.8rem;
}

.hero-para {
    line-height: 1.6rem;
    font-size: 1.2rem;
}

.hero-order-btn {
    border: 3px solid var(--web-orange);
    border-radius: 6px;
    background-color: var(--web-orange);
    padding: 14px 28px;
    font-size: 1rem;
    color: var(--white);
    margin-top: 12px;
}

.hero-order-btn:hover {
    background-color: var(--white);
    color: var(--web-orange);
}

/* menu page section*/
.menu-section {
    background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.7)), var(--menu-bg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    padding: 0 15px;
    overflow: auto;
}

.menu-heading {
    color: var(--white);
    font-size: 2.5rem;
    padding: 20px;
    text-align: center;
}

.menu-card-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min(100%, 26rem), 1fr));
    gap: 30px;
    font-size: 1.3rem;
}

.menu-card {
    background: linear-gradient(to right, var(--platinum) 70%, var(--web-orange) 100%);
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-areas: "image title" "body body";
    gap: 5px;
    padding: 20px;
    border-radius: 12px;
}

.menu-card-img {
    width: 170px;
    height: 120px;
    border-radius: 20px;
}

.menu-card-img:hover {
    transform: scale(1.02);
    box-shadow: 0 6px 12px var(--oxford-blue);
}

.menu-card-price {
    font-size: 1.4rem;
    text-decoration: underline var(--web-orange);
    text-decoration-skip-ink: none;
    display: block;
    margin-top: 10px;
}

.menu-card-body {
    grid-area: body;
}

/* about page section*/
.aboutSection {
    width: 100vw;
    height: 100vh;
    background-color: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-container {
    width: 60vw;
    height: 80vh;
    background-color: var(--platinum);
    text-align: center;
    line-height: 1.3;
    padding: 18px;
    overflow: auto;
}

.about-heading {
    border-bottom: 4px solid var(--web-orange);
    display: inline-block;
    padding-bottom: 10px;
    margin-bottom: 25px;
    font-size: 4rem;
}

/* contact page section*/
.contact-section {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    padding: 5vh 20px;
    overflow: auto;
}

.contact-addr-title,
.contact-us-title {
    font-size: 3.5rem;
    text-decoration: underline 3px var(--web-orange);
    text-underline-offset: 8px;
}

.contact-address,
.contact-open-time {
    white-space: pre;
    padding: 12px 0;
    font-size: 1.2rem;
}

.contact-map {
    width: 45vw;
    max-height: 50vh;
    box-shadow: 2px 2px 1px 1px var(--platinum);
}

.contact-form {
    padding: 60px 20px 0;
}

.input-field {
    display: block;
    line-height: 3;
    width: 70%;
    margin-bottom: 20px;
}

.input-field:focus {
    background-color: var(--platinum);
    border-color: var(--black);
}

.contact-form button {
    padding: 10px 20px;
    letter-spacing: 2px;
    box-shadow: 2px 2px 1px 1px var(--platinum);
}

.contact-form button:hover {
    background: var(--oxford-blue);
    color: var(--white);
}

/* footer section*/
footer {
    background-color: var(--oxford-blue);
    color: var(--white);
    font-size: 1.3rem;
    padding: 8px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.github-icon {
    height: 30px;
    width: 30px;
}

.github-icon:hover {
    transform: scale(1.2) rotate(180deg);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAEA;;;IAGI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,kDAAsC;IACtC,kDAAoC;;IAEpC,uJAAuJ;IACvJ,4BAA4B;;IAE5B,YAAY;IACZ,gBAAgB;IAChB,sBAAsB;IACtB,qBAAqB;IACrB,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,0DAA0D;AAC9D;;AAEA;IACI,eAAe;AACnB;;AAEA,WAAW;AACX;IACI,oCAAoC;IACpC,aAAa;IACb,8BAA8B;IAC9B,eAAe;IACf,wCAAwC;AAC5C;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,oCAAoC;IACpC,YAAY;IACZ,iBAAiB;IACjB,sBAAsB;AAC1B;;AAEA;IACI,+BAA+B;IAC/B,mCAAmC;IACnC,WAAW;IACX,cAAc;IACd,WAAW;IACX,QAAQ;AACZ;;AAEA;IACI,WAAW;AACf;;AAEA,0BAA0B;AAC1B;IACI,mFAAmF;IACnF,sBAAsB;IACtB,2BAA2B;IAC3B,4BAA4B;IAC5B,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,mBAAmB;IACnB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,mCAAmC;IACnC,kBAAkB;IAClB,mCAAmC;IACnC,kBAAkB;IAClB,eAAe;IACf,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,8BAA8B;IAC9B,wBAAwB;AAC5B;;AAEA,qBAAqB;AACrB;IACI,mFAAmF;IACnF,sBAAsB;IACtB,2BAA2B;IAC3B,4BAA4B;IAC5B,aAAa;IACb,eAAe;IACf,cAAc;AAClB;;AAEA;IACI,mBAAmB;IACnB,iBAAiB;IACjB,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sEAAsE;IACtE,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,kFAAkF;IAClF,aAAa;IACb,8BAA8B;IAC9B,8CAA8C;IAC9C,QAAQ;IACR,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;IACtB,yCAAyC;AAC7C;;AAEA;IACI,iBAAiB;IACjB,4CAA4C;IAC5C,8BAA8B;IAC9B,cAAc;IACd,gBAAgB;AACpB;;AAEA;IACI,eAAe;AACnB;;AAEA,sBAAsB;AACtB;IACI,YAAY;IACZ,aAAa;IACb,8BAA8B;IAC9B,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,iCAAiC;IACjC,kBAAkB;IAClB,gBAAgB;IAChB,aAAa;IACb,cAAc;AAClB;;AAEA;IACI,0CAA0C;IAC1C,qBAAqB;IACrB,oBAAoB;IACpB,mBAAmB;IACnB,eAAe;AACnB;;AAEA,wBAAwB;AACxB;IACI,YAAY;IACZ,aAAa;IACb,aAAa;IACb,2DAA2D;IAC3D,iBAAiB;IACjB,cAAc;AAClB;;AAEA;;IAEI,iBAAiB;IACjB,gDAAgD;IAChD,0BAA0B;AAC9B;;AAEA;;IAEI,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,2CAA2C;AAC/C;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,